@font-face {
  font-family: platformR;
  font-weight: 400;
  font-style: normal;
  src: url(./Platform-Regular-Web.woff) format("woff")
}

@font-face {
  font-family: fgl;
  font-weight: 400;
  font-style: normal;
  src: url(FoundersGroteskLight.woff2) format("woff2")
}

@font-face {
  font-family: fgm;
  font-weight: 400;
  font-style: normal;
  src: url(FoundersGroteskMedium.woff2) format("woff2")
}

@font-face {
  font-family: nhur;
  font-weight: 400;
  font-style: normal;
  src: url(NeueHaasUnicaRegular.woff2) format("woff2")
}

* {
  -webkit-touch-callout: none;
  -webkit-user-select: none;
  -khtml-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: rgb(236, 235, 209);
  width: 100vw;
  height: 100vh;
}

.App {
  display: inline-block;
  width: 100vw;
  height: auto;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.lateralBar {
  display: inline-block;
  width: fit-content;
  height: auto;
  vertical-align: top;
  margin: 20px 40px 0 40px;
}

.header {
  margin: 0;
  display: block;
  color: #433f3e;
  font: 300% platformR;
}

.header > h1 {
  margin: 0;
}

.scoreSheet {
  display: inline-block;
  margin: 20px 0 0 0;
  width: 350px;
  height: 400px;
  background-color: rgb(250, 250, 250);
  border: 1px solid #e6e6e6;
  box-shadow: 
              0 1px 1px rgba(0,0,0,0.15), 
              0 10px 0 -5px rgb(250, 250, 250), 
              0 10px 1px -4px rgba(0,0,0,0.15), 
              0 20px 0 -10px rgb(248, 248, 248), 
              0 20px 1px -9px rgba(0,0,0,0.15);
}

.scoreSheetHeader {
  color: #6b6665;
  font: 1.6rem roboto;
  margin: 30px 20px 00px 20px;
  padding-bottom: 5px;
  text-align: center;
  /* text-transform: uppercase; */
  border-bottom: 2px solid rgb(214, 213, 189);
}

.scoreSheetTable {
  margin: auto;
  width: 88.5%;
}

.moves {
  position: relative;
  display: inline-block;
  width: 49.68%;
  height: 310px;
  margin: 0;
}

.colorTag {
  width: 100%;
  height: 2px;
  background-color: rgba(163, 135, 94, 0.8);
  visibility: hidden;
}

#whiteTag {
  background-color: rgba(239,237,209, 0.8);
}

.scoreSheetDivision {
  display: inline-block;
  background-color: rgb(214, 213, 189);
  width: 2px;
  height: 310px;
}

.board {
  position: relative;
  /*width: 600px;
  height: 600px;*/
  margin-right: 0;
  margin-top: 20px;
  margin-left: 40px;
  background-color: #5f4c36;
  display: inline-flex;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
  flex-direction: column-reverse;
  border: 5px solid #5f4c36;/*#6e6966*/
  border-radius: 5px;
  box-shadow: 0 0 20px 3px rgb(182, 182, 182);
}

.square {
  margin: 0;
  /*width: 75px;
  height: 75px;*/
  position: relative;
}

.square.dark {
  background-color: rgb(163, 135, 94);
  color: rgb(239,237,209);
}

.square.light {
  background-color:rgb(239,237,209);/* rgb(247, 245, 225);*/
  color: rgb(163, 135, 94)/*rgb(120, 150, 86);*/
}

#a1 {
  border-radius: 0 0 0 2px;
}

#a8 {
  border-radius: 2px 0 0 0;
}

#h1 {
  border-radius: 0 0 2px 0;
}

#h8 {
  border-radius: 0 2px 0 0;
}

#characterID {
  position: absolute;
  bottom: 2px;
  right: 5px;
  font-size: 0.75rem;
  font-weight: 600;
  /* text-transform: uppercase; */
  -webkit-touch-callout: none;
  -webkit-user-select: none;
  -khtml-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
}

.piece {
  display: block;
  position: relative;
  background-color: transparent;
  margin: 3%;
  width: 94%;
  filter: /*contrast(85%)
          sepia(50%)
          contrast(118%)*/
          drop-shadow(0px 0px 1px rgba(0,0,0,0.5));
  z-index: 20;
}
/*
#bp {
  filter: hue-rotate(90deg);
}
*/
.square.dark > .piece.select {
  -webkit-filter: drop-shadow(1px 1px 3px rgb(180,180,180))
                  drop-shadow(-1px -1px 3px rgb(180,180,180));
  filter: drop-shadow(1px 1px 3px rgb(180,180,180)) 
          drop-shadow(-1px -1px 3px rgb(180,180,180));
}

.square.light > .piece.select {
  -webkit-filter: drop-shadow(1px 1px 3px rgb(100,100,100))
                  drop-shadow(-1px -1px 3px rgb(100,100,100));
  filter: drop-shadow(1px 1px 3px rgb(100,100,100)) 
          drop-shadow(-1px -1px 3px rgb(100,100,100));
}

#wp, #bp {
  margin-left: 4%;
}

.selection {
  position:absolute;
  filter: drop-shadow(1px 1px 2px rgba(0,0,0,0.5));
  z-index: 10;
}
